    transition: all 0.3s ease 0s;
}

.enabled:hover{
    background-color: #81d2e9;
    box-shadow: 0px 15px 20px rgba(117, 197, 219, 0.4);
    color: #fff;
    -webkit-transform: translateY(-7px);
            transform: translateY(-7px);
}

.disabled {
    opacity: 30%;
    pointer-events: none;
    cursor: not-allowed;  
    background-color: #9e9e9e;
    color: black;
}
